.css {
  margin-top: 1em;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: #000c;
  z-index: 2;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  max-height: 200px;
  max-width: 300px;
}

.css__code {
  padding: 1em;
  background: #0005;
  white-space: nowrap;
  overflow: auto;
}

.css__button {
  margin: 1em;
  background: none;
  border: none;
  outline: none;
  color: #9cf0ff;
}

@media (min-width: 600px) {
}

@media (min-width: 900px) {
  .css {
    left: 0;
    -webkit-transform: translate(0);
        -ms-transform: translate(0);
            transform: translate(0);
    background: none;
    position: relative;
    grid-area: 2 / 3;
    max-width: 100%;
    margin-top: -10%;
  }
}



